
.card-image{
  max-width: 100%;
  margin-top: 5%;
  max-height: 87%
}

.pointer{
    cursor: pointer;
}


/* Based on https://codepen.io/shayhowe/pen/Fvjnf */

.card-container {
    height: 80%;
    width: 60%;
    -webkit-perspective: 600;
            perspective: 600;
    position: relative;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
  }

  .card {
    height: 100%;
    position: absolute;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    width: 100%;
  }

  .flip {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .card .side {
    background: #eaeaed;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 6px;
    height: 100%;
    position: absolute;
    overflow: hidden;
    width: 100%;
  }
  .card .back {
    background: #eaeaed;
    color: #0087cc;
    text-align: center;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }

.matched{
    background:  rgba(179, 104, 12, 0.61);;
    border-radius: 6px;
    height: 100%;
    width: 100%;

  }


